import React, { useEffect, useRef } from 'react';
import * as echarts from 'echarts';

interface PrCurveChartProps {
  data: Array<[number, number]> | null;
}

const PrCurveChart: React.FC<PrCurveChartProps> = ({ data }) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chartInstance = useRef<echarts.ECharts | null>(null);

  useEffect(() => {
    // 
    if (chartRef.current) {
      chartInstance.current = echarts.init(chartRef.current);
    }

    // 
    const handleResize = () => {
      chartInstance.current?.resize();
    };
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      chartInstance.current?.dispose();
    };
  }, []);

  useEffect(() => {
    if (!chartInstance.current || !data) return;

    // AP (Average Precision)
    let ap = 0;
    for (let i = 1; i < data.length; i++) {
      ap += (data[i][1] + data[i-1][1]) * Math.abs(data[i][0] - data[i-1][0]) / 2;
    }

    //  [recall, precision]
    const prData = data.map(point => [point[0], point[1]]);

    const option = {
      title: {
        text: `PR (AP = ${ap.toFixed(3)})`,
        left: 'center',
        top: 20,
        textStyle: {
          fontSize: 16,
          fontWeight: 'bold'
        }
      },
      tooltip: {
        trigger: 'axis',
        formatter: function (params: any) {
          const point = params[0];
          return `: ${(point.data[0] * 100).toFixed(2)}%<br/>: ${(point.data[1] * 100).toFixed(2)}%`;
        }
      },
      legend: {
        data: ['PR', ''],
        top: 50
      },
      grid: {
        left: '10%',
        right: '10%',
        bottom: '15%',
        top: '20%',
        containLabel: true
      },
      xAxis: {
        type: 'value',
        name: ' (Recall)',
        nameLocation: 'middle',
        nameGap: 30,
        min: 0,
        max: 1,
        axisLabel: {
          formatter: function(value: number) {
            return (value * 100).toFixed(0) + '%';
          }
        }
      },
      yAxis: {
        type: 'value',
        name: ' (Precision)',
        nameLocation: 'middle',
        nameGap: 50,
        min: 0,
        max: 1,
        axisLabel: {
          formatter: function(value: number) {
            return (value * 100).toFixed(0) + '%';
          }
        }
      },
      series: [
        {
          name: 'PR',
          type: 'line',
          data: prData,
          smooth: true,
          lineStyle: {
            color: '#10b981',
            width: 3
          },
          itemStyle: {
            color: '#10b981'
          },
          areaStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              { offset: 0, color: 'rgba(16, 185, 129, 0.3)' },
              { offset: 1, color: 'rgba(16, 185, 129, 0.1)' }
            ])
          }
        },
        {
          name: '',
          type: 'line',
          data: [[0, 0.1], [1, 0.1]], // 10%
          lineStyle: {
            color: '#ef4444',
            width: 2,
            type: 'dashed'
          },
          itemStyle: {
            color: '#ef4444'
          }
        }
      ]
    };

    chartInstance.current.setOption(option);
  }, [data]);

  if (!data) {
    return <div className="text-center py-8 text-gray-500"></div>;
  }

  // AP
  let ap = 0;
  for (let i = 1; i < data.length; i++) {
    ap += (data[i][1] + data[i-1][1]) * Math.abs(data[i][0] - data[i-1][0]) / 2;
  }

  return (
    <div>
      <div className="mb-4 p-4 bg-green-50 rounded-lg">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
          <div>
            <span className="text-gray-600">AP:</span>
            <span className="ml-2 font-semibold text-green-600">{ap.toFixed(3)}</span>
          </div>
          <div>
            <span className="text-gray-600">:</span>
            <span className="ml-2 font-semibold">
              {ap >= 0.8 ? '' : ap >= 0.6 ? '' : ap >= 0.4 ? '' : ''}
            </span>
          </div>
          <div>
            <span className="text-gray-600">:</span>
            <span className="ml-2 font-semibold">{data.length}</span>
          </div>
        </div>
      </div>
      <div ref={chartRef} style={{ width: '100%', height: '400px' }} />
    </div>
  );
};

export default PrCurveChart;