import React, { useEffect, useRef } from 'react';
import * as echarts from 'echarts';

interface Correlation {
  feature1: string;
  feature2: string;
  correlation: number;
}

interface FeatureCorrelationChartProps {
  correlations: Correlation[];
}

const FeatureCorrelationChart: React.FC<FeatureCorrelationChartProps> = ({ correlations }) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chartInstance = useRef<echarts.ECharts | null>(null);

  useEffect(() => {
    // 
    if (chartRef.current) {
      chartInstance.current = echarts.init(chartRef.current);
    }

    // 
    const handleResize = () => {
      chartInstance.current?.resize();
    };
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      chartInstance.current?.dispose();
    };
  }, []);

  useEffect(() => {
    if (!chartInstance.current || !correlations || correlations.length === 0) {
      return;
    }

    // 
    const features = Array.from(
      new Set(
        correlations.flatMap(corr => [corr.feature1, corr.feature2])
      )
    ).sort();

    // 
    const matrix: number[][] = Array(features.length)
      .fill(0)
      .map(() => Array(features.length).fill(1)); // 1

    // 
    correlations.forEach(corr => {
      const i = features.indexOf(corr.feature1);
      const j = features.indexOf(corr.feature2);
      if (i !== -1 && j !== -1) {
        matrix[i][j] = corr.correlation;
        matrix[j][i] = corr.correlation; // 
      }
    });

    // 
    const data: [number, number, number][] = [];
    for (let i = 0; i < features.length; i++) {
      for (let j = 0; j < features.length; j++) {
        data.push([i, j, matrix[i][j]]);
      }
    }

    const option = {
      tooltip: {
        position: 'top',
        formatter: function (params: any) {
          const i = params.data[0];
          const j = params.data[1];
          const value = params.data[2];
          return `${features[i]}  ${features[j]} : ${value.toFixed(2)}`;
        }
      },
      grid: {
        left: '10%',
        right: '10%',
        top: '10%',
        bottom: '15%'
      },
      xAxis: {
        type: 'category',
        data: features,
        splitArea: {
          show: true
        },
        axisLabel: {
          rotate: 45,
          fontSize: 10
        }
      },
      yAxis: {
        type: 'category',
        data: features,
        splitArea: {
          show: true
        },
        axisLabel: {
          fontSize: 10
        }
      },
      visualMap: {
        min: -1,
        max: 1,
        calculable: true,
        orient: 'horizontal',
        left: 'center',
        bottom: '0%',
        inRange: {
          color: ['#2c7bb6', '#ffffbf', '#d7191c']
        }
      },
      series: [
        {
          name: '',
          type: 'heatmap',
          data: data,
          label: {
            show: true,
            formatter: function (params: any) {
              return params.data[2].toFixed(2);
            },
            fontSize: 8
          },
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    };

    chartInstance.current.setOption(option);
  }, [correlations]);

  return <div ref={chartRef} style={{ width: '100%', height: '400px' }} />;
};

export default FeatureCorrelationChart;